.register {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.registerForm {
    width: 50%;
  display: flex;
  flex-direction: column;
  font-family: "Didact Gothic", sans-serif;
}
.registerLoginButton {
  position: absolute;
  top: 80px;
  right: 50px;
  width: 150px;
}
.registerInput {
  height: 33px;
  margin: 12px 0;
  font-size: 20px;
  padding: 10px;
  background-color: #3d3146;
  border: none;
  color: #ffffffa8;
  font-size: 16px;
  border-bottom: 2px solid #ca7f24;
  border-radius: 24px;
}
.registerInput:focus {
  outline: none;
}
.registerLabel {
  margin: 12px 0;
  font-size: 20px;
  font-weight: 500;
  color: #ca7f24;
}

.registerButton, .registerLoginButton {
  height: 50px;
  font-size: 16px;
  margin-top: 20px;
  padding: 10px;
  border: none;
  background-color: #ca7f24;
  color: #ffffffa8;
  border-radius: 24px;
  cursor: pointer;
}
.registerButton:hover, .registerLoginButton:hover {
  background-color: #ffffffa8;
  color: #ca7f24;
}
.registerTitle{
  color: #ca7f24;
  font-size: 50px;
  font-family: "Poiret One", cursive;
  font-weight: bold;
}

@media screen and (max-width: 792px){
  .registerForm{
      width: 70%;
  }
  .registerButton, .registerLoginButton {
    height: 42px;
    padding: 0;
  }
  .registerTitle{
    font-size: 38px;
  }
  .registerInput{
    height: 26px;
    margin: 0;
  }
  .register{
    height: 80vh;
  }
}
