video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
  /* margin-top: 50px; */
}

.overlay {
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.overlay h1 {
  /* background: #000 none repeat scroll 0 0; */
  color: rgba(255, 255, 255, 0.9);
  /* font-family: 'Didact Gothic', sans-serif; */
  font-family: "Poiret One", cursive;
  font-size: 6.5rem;
  margin: 2rem 3rem 0;
  mix-blend-mode: overlay;
  padding: 5px 15px;
  text-align: center;
  display: flex;
  /* justify-content: center; */
  align-items: center;
}

@media screen and (max-width: 670px) {
  .overlay h1 {
    margin-bottom: 283px;
    font-size: 5.5rem;
  }
}
@media screen and (max-width: 520px) {
  .overlay h1 {
    margin-bottom: 283px;
    font-size: 3.5rem;
  }
}
